.game-board {
  height: 100%;
  width: 100%;
  position: relative;
  
  /* Hide scrollbar for IE, Edge and Firefox */
  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */
}

/* Hide scrollbar for Chrome, Safari and Opera */
.game-board::-webkit-scrollbar {
  display: none;
}

.hideScrollBar::-webkit-scrollbar {
  display: none !important;
}

.game-piece {
  position: absolute;
}

svg .hexagons {}

svg .hexagons g {
  fill: rgba(0,0,0,0);
  fill-opacity: 0.6;
}

svg .hexagons g:hover {
  fill: rgba(255,255,255,0.15);
  fill-opacity: 0.0;
}

svg .hexagons g:hover text {
  fill-opacity: 1;
}

svg .hexagons g polygon {
  stroke: rgba(255,255,255,0.1);
  stroke-width: 0.2;
  transition: fill-opacity .5s;
}

svg .hexagons g polygon:hover {
  stroke: rgba(0,255,255,0.35);
  stroke-width: 0.2;
  transition: fill-opacity 1.5s;
}

svg .hexagons g text {
  font-size: 0.06em;
  fill: white;
  fill-opacity: 0.7;
  transition: fill-opacity .5s;
}
svg .hexagons path {
  fill: none;
  stroke: rgba(255,255,255,0.05);
  stroke-width: 0.2em;
  stroke-opacity: 0.7;
  stroke-linecap: round;
  stroke-linejoin: round;
}

svg .hexagons g.blocked {
    fill: #465880;
}
svg .hexagons g.blocked:hover {
    fill: #53699a;
}

/* Change Autocomplete styles in Chrome*/
input:-webkit-autofill,
input:-webkit-autofill:hover, 
input:-webkit-autofill:focus,
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover,
textarea:-webkit-autofill:focus,
select:-webkit-autofill,
select:-webkit-autofill:hover,
select:-webkit-autofill:focus {
  border: 1px solid white;
  -webkit-text-fill-color: white;
  box-shadow: 0 0 0px 1000px rgba(0,0,0,0) inset !important;
  -webkit-transition: background-color 5000s ease-in-out 0s;
  transition: background-color 5000s ease-in-out 0s;
}
